.head{
    text-align: center;
    height: 60px;
    padding: 0 15px;
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid rgba(68, 59, 59, 0.2);
}
.head__title{
    margin-top: 16px;
    color: white
}
ul{
    list-style: none;
    display: flex;
}
ul li{
    margin-left: 10px;
}
ul li .head__btn_modal{
    position: relative;
}
ul li .head__btn_icon{
    font-size: 1.2rem;
    text-align: center;
    position: absolute;
    top:6px;
    left:8px;
}
.head__btn_modal{
    background: none rgba(255, 255, 255, 0.1);
    border: none;
    color: white;
    padding: 8px 12px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 13px;
    border-radius: 8px;
    padding-left: 32px;
}

@media (max-width:660px){
    .head{
        padding: 0;
    }
    .head__title{
        margin-left: 10px;
        margin-top: 10px;
        font-size: 0;
    }
    .head__title span{
        font-size: 35px;
    }
    .head__btn_modal span{
        font-size: 0;
        padding: 0px;
    }
    ul li{
        margin-left: 10px;
    }
    ul li .head__btn_modal i{
        font-size: 1.5rem;
        text-align: center;
        top:3px;
        left:11px;
    }
}