
.tasks__wrapper{
    max-width: 560px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
}
.right-content{
    display: inline-block;
}
.tasks__item-list{
    box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
    border-radius: 10px;
    color: rgb(0, 0, 0);
    margin: 5px 0;
    padding: 10px;
    background-color: #fafafa;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.content-left{
    display: flex;
    align-items: center;
}
.btn-ok{
    padding:8px;
    font-size: 20px;
    cursor: pointer;
    background:#fafafa;
    border:none;
}
.textToDo{
    display: inline-block;
}
.textToDo:first-letter{
    text-transform: uppercase;
}
.tasks__active-list{
    padding-left: 0;
    margin: 0 auto;
    width: 100%;
    flex-direction: column;
}
.tasks__button-add-task{
    font-size: 20px;
    width: 100%;
    height: 60px;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    display: flex;
    -webkit-box-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    align-items: center;
    cursor: pointer;
    opacity: 0.8;
    margin-top: 12px;
    border: 2px dashed rgba(255, 255, 255, 0.4);
    color: white;
    margin-bottom: 50px;
}