.modal__wrapper{
    position: fixed; 
    bottom: 0 ;
    width: 100%; 
    background-color: rgba(0, 0, 0, .2);
    backdrop-filter: blur(10px);
}
.modal__wrapper.open{
    height: 100%;
    display: block;
}
.modal__wrapper.close{
    height: 0;
    display: none;
}
.modal__body{
    max-width: 500px;
    margin: 0 auto;
    background: white;
    margin-top: 10%;
    padding: 20px;
    border-radius: 10px;
    max-height: 500px;
    overflow-y: scroll;
}
.modal__body::-webkit-scrollbar{
    width: 0;
    background: transparent;
}
.modal__body{
    -ms-overflow-style: none;
    scrollbar-width: none;
}
.modal__close{
    text-align: right;
    margin-top: -15px;
    font-size: 40px;
    color: gray;
    cursor: pointer;
}
.modal__body h2 {
    text-align: center;
    width: 100%;
}
.mode__double-border-button {
    width: 60%;;
    text-align: center;
    text-decoration: none;
    display:block;
    margin: 30px auto;
    padding: 10px 30px;
    position: relative;
    border: 1px solid #000000;
    color: #000000;
    font-family: 'Montserrat', sans-serif;
    transition: .4s;
}
.mode__double-border-button:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    margin: auto;
    border: 1px solid rgba(0, 0, 0, 0);
    transition: .4s;
}
.mode__double-border-button:hover:after {
    border-color: #000000;
    width: calc(100% - 10px);
    height: calc(100% + 10px);
}
.modal__input {
    font-size: 20px;
    width: 50px;
    outline:none;
    border: 1px solid rgb(133, 133, 133);
    text-align: center;
}
.modal__time-wrapper{
    max-width: 450px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
}
.modal__time-wrapper h2{
    font-weight: 500;
    text-align: left;
}
.modal__modes{
    text-align: center;
}
.modal__time-mode-wrapper{
    align-items: center;
    display: flex;
    justify-content: space-around;
}

@media (max-width:660px){
    .modal__body{
        margin-top: 25%;
    }
}